<template>
      <div class="m_detail">
          <div class="m_title">
              &nbsp;{{data.meetingroom_name}}
          </div>
          <div class="m_info">
              可容纳人数：  {{data.meetingroom_size}}人  <span>面积: {{data.meetingroom_info}}平方米</span>
          </div>
          <div class="m_info">
              价格： {{data.price}}元/每小时
          </div>
          <div class="m_info">
              地址： {{data.address}}
          </div> 
          <div class="m_info">
                电话：<a style="color: blue" href="tel://15099909769">15099909769</a>
          </div> 
           <div class="res_times"  v-if="type == 'order'">预订时间：{{data.time}} <span :class="{active: data.state == 3}" v-html="data.state == 0 ? '待审核' : data.state == 1? '通过' : data.state == 2?'已拒绝': '已取消'"></span></div>
          <div class="m_swipe">
            <van-swipe indicator-color="white" >
                <van-swipe-item  v-for="(image, index) in data.content" :key="index" v-if="data.content.length">
                    <img :src="image.picUrl.op_opic" />
                    <div style="margin-top: 10px;" class="img_desc">详细描述</div>
                    <div class="img_desc">{{image.des}}</div>
                </van-swipe-item>
                </van-swipe>
            <div>&nbsp;</div>
            <div>&nbsp;</div>
          </div>
    <div class="fix_bottom" v-if="type == 'detail'">
        <div class="res_btn" @click="goNext()">
            立即预订
        </div>
    </div>
    <div class="fix_bottom" v-if="type == 'order' && data.state == 0">
        <div class="res_btn" @click="doCancel()" style="background: #f9fbf9; border: 1px solid #eee; color: #4bcf96">
            取消预订
        </div>
    </div>
    </div>

</template>
<script>
  export default {
    name: 'm_detail',
    data() {
      return {
          id: this.$route.params.mid || 1,
          type: this.$route.params.mtype || 'detail',
          data: {}

      }
    },
    created() { 
        this.getData();
    },
    methods: {
        getData() {
            var url = '/app/meeting/detail?id='+this.id;
            if(this.type == 'order') {
                url = '/app/meeting/reserveOrder?id='+this.id;
            }
            this.axios.get(url).then(res => {
                if (res.s == 1) { 
                    this.data = res.d;
                    
                } 
          }) 
        },
        goNext() {
              this.$router.push({
                path: '/reservation/'+this.id,
              })
        },
        doCancel() {
            this.$dialog.confirm({
                title: '确认取消预订?',
                message: ''
                }).then(() => {
                    this.axios.get('/app/meeting/cancel?id='+this.id).then(res => {
                        if (res.s == 1) {  
                            this.$toast('取消成功!'); 
                            this.getData();
                            return;
                        } 
                    }) 
                }).catch(() => {
                // on cancel
                });
      
        }
        
    }
  }
</script>
<style>
    .van-swipe__track  {
        padding-top: 30px;

    }
    .van-swipe__indicators {
        top: 12px;

    }
    .van-swipe__indicator {
        background-color: #eeeeee;
    }
    .van-swipe__indicator--active {
        background-color: #dddddd;
        width: 14px;
        border-radius: 14px;
    }
</style>
<style lang="scss" scoped>
 @function rem($px) {
    @return $px / 50px * 1rem;
}
  .m_detail {
      background: #fff;
      min-height: 100%;
      padding: rem(15px) 0 rem(220px) 0;
      .m_title {
          padding: 0 rem(15px);
          font-size: rem(20px);
          color: #40413f;
          margin-bottom: rem(6px);
          &:before {
              content: ' ';
              display: inline-block;
              width: rem(10px);
              height: rem(10px);
              border-radius: rem(10px);
              background: #4bcf96;

          }

      }
      .m_info {
          padding: 0 rem(15px);
          color: #8a8c89;
          font-size: rem(14px);
          padding-top: rem(4px);
          span {
              float: right;

          }

      }
      .res_times {
          padding: rem(15px) rem(15px) 0 rem(15px); 
          font-size: rem(14px);
          border-top: rem(1px) solid #eee;
          margin-top: rem(15px);
          span {
              float: right;
              color: #4bcf96;
              &.active{
                color: #d0d2cf;

              }
          }

      }
  
      .m_swipe {
        padding: 0 rem(15px);
        border-top: rem(1px) solid #eee;
        margin-top: rem(20px);
          background: #fff;
         
      }
      .van-swipe {
        img {
            width: 100%;
        }

      }

      .img_desc {
          font-size: rem(16px);

      }
    .fix_bottom {
      position: fixed;
      bottom: 0;
      width: 100%;
      height: rem(75px);
      border-top: rem(1px) solid #eee;
      text-align: center;
      background: #fff;
    }
    .res_btn {
            font-size: rem(16px);
            background: #4bcf96;
            color: #fff;
            height: rem(50px);
            line-height: rem(50px);
            width: 80%;
            border-radius: rem(50px);
            margin: 0 auto;
            margin-top: 12px;
    }
  }

</style>
